import { useState, useEffect, useRef, useContext } from 'react'
import Button from '../../components/ui/Button/Button'
import { useFetching } from '../../hooks/useFetching'
import TestingService from '../../api/TestingService'
import Select from '../../components/ui/Select/Select'
import { useParams } from 'react-router-dom'
import { Controller, useForm } from 'react-hook-form';
import DatePicker from '../../components/ui/DatePicker/DatePicker'
import TestingsListForUko from '../../components/uko/TestingsListForUko'
import DekanatService from '../../api/DekanatService'
import { AppContext } from '../../context'
import Loader from "../../components/ui/Loader/Loader"

const UkoPage = () => {
  const { userFilialId } = useContext(AppContext)

  const urlParans = useParams()
  const userId = urlParans.id

  const [ttSessions, setTtSessions] = useState([])
  const [ttSessionsWithFilter, setTtSessionsWithFilter] = useState([])

  const resetSelectValue = (selectRef, setOptionsState = null) => {
    selectRef.current.setValue(null, "onChange")
    setOptionsState && setOptionsState([])
  }

  const [testings, setTestings] = useState([])
  const [testingsWithFilter, setTestingsWithFilter] = useState([])

  const facultySelectForFilterRef = useRef(null)
  const deptSelectForFilterRef = useRef(null)
  const datePickerBeforeForFilterRef = useRef(null)
  const datePickerAfterForFilterRef = useRef(null)

  const [getListTestsToCreate, isSessionLoading] = useFetching(async (filId) => {
    const response = await TestingService.getListTestsToCreate(filId)

    if (response.status == 200) {
      setTtSessions(response.data)
      setTtSessionsWithFilter(response.data)
    }
  })

  const [getTestingsByEmployeeId, isTestingsLoading] = useFetching(async (employeeId) => {
    const response = await TestingService.getTestingsByEmployeeId(employeeId)

    if (response.status == 200) {
      setTestings(response.data)
      setTestingsWithFilter(response.data)
    }
  })

  const [facultyId, setFacultyId] = useState(null)
  const [faculties, setFaculties] = useState([])
  const [getFaculties, isFacultiesLoading, facError] = useFetching(async () => {
    const response = await DekanatService.getFaculties()
    const dataArr = []
    response.data.forEach(dataItem => {
      dataArr.push({
        value: dataItem.facId,
        label: dataItem.facName
      })
    })

    setFaculties(dataArr)
  })

  const [deptId, setDeptId] = useState(null)
  const [departments, setDepartments] = useState([])
  const [getDepartmentsByFacultyId, isDepartmentsLoading] = useFetching(async (facId) => {
    const response = await DekanatService.getDepartmentsByFacultyId(facId)
    const dataArr = []
    response.data.forEach(dataItem => {
      dataArr.push({
        value: dataItem.departmentId,
        label: dataItem.deptName
      })
    })

    setDepartments(dataArr)
  })


  useEffect(() => {
    if (facultyId) {
      resetSelectValue(deptSelectForFilterRef, setDepartments)
      getDepartmentsByFacultyId(facultyId)
    }
  }, [facultyId])

  useEffect(() => {
    getFaculties()

    getListTestsToCreate(parseInt(userFilialId))
    getTestingsByEmployeeId(userId)
  }, [])

  const { control: controlFilter, handleSubmit: handleSubmitFilter, reset: resetFilterForm } = useForm({
    mode: "onSubmit",
  })

  const onTestingAndSessionFilter = (data) => {
    let newTestingFiltersData = testings
    let newSessionFiltersData = ttSessions
  
    if (data.facultyId) {
      newTestingFiltersData = testings.filter(t => t.facultyId == data.facultyId)
      newSessionFiltersData = ttSessions.filter(t => t.caseSFacultyId == data.facultyId)
      
    }

    if (data.deptId) {
      newTestingFiltersData = newTestingFiltersData.filter(t => t.departmentId == deptId)
      newSessionFiltersData = ttSessions.filter(t => t.deptId == data.deptId)
    }

    if (data.examDateBefore) {
      const beforeDateTime = new Date(data.examDateBefore).getTime()
      newTestingFiltersData = newTestingFiltersData.filter(t => new Date(t.testingDate).getTime() >= beforeDateTime)
      newSessionFiltersData = newSessionFiltersData.filter(t => new Date(t.startEvent).getTime() >= beforeDateTime)
    }

    if (data.examDateAfter) {
      const afterDateTime = new Date(data.examDateAfter).getTime()
      newTestingFiltersData = newTestingFiltersData.filter(t => new Date(t.testingDate).getTime() <= afterDateTime)
      newSessionFiltersData = newTestingFiltersData.filter(t => new Date(t.startEvent).getTime() <= afterDateTime)
    }

    setTestingsWithFilter(newTestingFiltersData)
    setTtSessionsWithFilter(newSessionFiltersData)
  }

  const resetFilter = (evt) => {
    resetFilterForm(); //  

    evt.preventDefault()
    facultySelectForFilterRef.current.clearValue()
    resetSelectValue(deptSelectForFilterRef, setDepartments)
    datePickerBeforeForFilterRef.current.setSelected(null)
    datePickerAfterForFilterRef.current.setSelected(null)
    setTestingsWithFilter(testings)
    setTtSessionsWithFilter(ttSessions)
  }

  return (
    <>
      <div className="container">
        <div className='testing__filter filter-testing'>
          <form className='filter-testing__form form' onSubmit={handleSubmitFilter(onTestingAndSessionFilter)}>
            <label className='form__label'>
              <span className='form__text'></span>
              <Controller
                control={controlFilter}
                name='facultyId'
                render={({ field: { onChange } }) => (
                  <Select
                    ref={facultySelectForFilterRef}
                    onChange={newValue => {setFacultyId(newValue?.value); onChange(newValue?.value)}}
                    placeholder=' '
                    options={faculties}
                    isLoading={isFacultiesLoading}
                    isDisabled={isFacultiesLoading}
                  />
                )}
              />
            </label>
            <label className='form__label'>
              <span className='form__text'></span>
              <Controller
                control={controlFilter}
                name='deptId'
                render={({ field: { onChange } }) => (
                  <Select
                    ref={deptSelectForFilterRef}
                    onChange={newValue => {setDeptId(newValue?.value); onChange(newValue?.value)}}
                    placeholder=' '
                    options={departments}
                    isLoading={isDepartmentsLoading}
                    isDisabled={isDepartmentsLoading}
                  />
                )}
              />
            </label>
            <label className='form__label'>
              <span className='form__text'></span>
              <Controller
                control={controlFilter}
                name='examDateBefore'
                render={({ field: { onChange } }) => (
                  <DatePicker
                    ref={datePickerBeforeForFilterRef}
                    showTimeSelect={false}
                    onChange={(newDate) => onChange(newDate)}
                  />
                )}
              />
            </label>
            <label className='form__label'>
              <span className='form__text'></span>
              <Controller
                control={controlFilter}
                name='examDateAfter'
                render={({ field: { onChange } }) => (
                  <DatePicker
                    ref={datePickerAfterForFilterRef}
                    showTimeSelect={false}
                    onChange={(newDate) => onChange(newDate)}
                  />
                )}
              />
            </label>
            <div className='form__btns'>
              <Button className='form__btn'>
                
              </Button>
              <Button className='form__btn btn_secondary' onClick={(evt) => { resetFilter(evt) }}>
                
              </Button>
            </div>
          </form>
        </div>
        <div className='testings'>
          <h1 className='testings__title title'></h1>
          {
            (isTestingsLoading && isSessionLoading) 
              ? <Loader text="  " /> 
              : <TestingsListForUko 
                  onRemoveTesting={(testingId) => {
                    getListTestsToCreate(parseInt(userFilialId));
                    setTestings(testings.filter(x => x.testingId != testingId));
                    setTestingsWithFilter(testings.filter(x => x.testingId != testingId))
                  }} 
                  ttSessions={ttSessionsWithFilter} testings={testingsWithFilter} />
          } 
        </div>
      </div>
    </>
  )
}

export default UkoPage