import FileButton from '../ui/FileButton/FileButton'
import { getTestingFormatDate } from '../../utils/testing'
import { useContext } from 'react'
import { UkoContext } from '../../context'
import SingleCheckBox from '../ui/SingleCheckBox/SingleCheckBox'
import Button from "../ui/Button/Button"

const SessionItem = ({ session, onLoadTest, onLoadTestMultiple }) => {

  const {selectedSessionsIds, setSelectedSessionsIds, selectedCurrentSessionId, setSelectedCurrentSessionId, isParsingLoading} = useContext(UkoContext)

  const getSelectedCurrentSessionId = (isChecked, sessionId) => {
    let res = null

    if (isChecked) {
      res = sessionId
    } else {
      if (selectedCurrentSessionId != sessionId) {
        res = selectedCurrentSessionId
      } else {
        res = selectedSessionsIds.length > 1 ? selectedSessionsIds[selectedSessionsIds.length - 2] : null
      }
    }
    
    return res
  }

  return (
    <li className="testings__item testings__item--disable">
      <span className='testings-item__date'>{getTestingFormatDate(session.startEvent)}</span>
      <div className='testings-item__data data'>
        <span className='data__stage'>{session.course}  {session.group && `${session.group} `}</span>
        <span className='data__department'>{session.deptName}</span>
      </div>
      <div className="testings-item__btns">
        <SingleCheckBox name="sessions" numberKey={session.id} value={session.id} onClick={(isChecked) => {
          setSelectedCurrentSessionId(getSelectedCurrentSessionId(isChecked, session.id));
          setSelectedSessionsIds(prev => isChecked ? [...prev, session.id] : prev.filter(x => x != session.id))
        }} />
        <div className='discipline-btn'>{session.subjectName}</div>
        {
          selectedSessionsIds.length > 0 
            ? 
            selectedCurrentSessionId == session.id && 
            <>
              <FileButton accept=".doc, .docx" isLoading={isParsingLoading} text=" " onSelected={(e) => onLoadTestMultiple(e.target.files[0], selectedSessionsIds)} />
              <Button className="btn_secondary" onClick={() => document.querySelectorAll('.checkbox-item._checked input').forEach(activeCheckbox => activeCheckbox.click())}></Button>
            </>
            : <FileButton accept=".doc, .docx" isLoading={isParsingLoading && selectedSessionsIds.length == 0 && selectedCurrentSessionId == session.id} className="_not-tests" onSelected={(e) => {
                setSelectedCurrentSessionId(session.id)
                onLoadTest(e.target.files[0], session)
              }} />
        }
      </div>
    </li>
  )
}

export default SessionItem