import clsx from 'clsx'
import closePopup from '../../../assets/img/close.svg'
import "./Popup.css"

const Popup = ({ className = "", children, id, active, setActive}) => {


  return (
    <div id={id} className={clsx(className, "popup", {
      _open: active
    })}>
        <div className="popup__body">
            <div className="popup__content">
                <button type="button" className="popup__close" onClick={() => setActive(false)}>
                    <img src={closePopup} alt="" />
                </button>
                {children}
            </div>
        </div>
    </div>
  )
}

export default Popup