import { Link } from "react-router-dom"
import clsx from "clsx"
import "./ScoreCircle.css"

const ScoreCircle = ({ defaultScore, isDisabled, isAllAnswersChecked, scores, statusAnswerBlank, dataForState }) => {

    return (
        <div className={clsx("score-circle", {
            _disable: statusAnswerBlank == "PassageBegun" || (statusAnswerBlank == "AutoChecked" && isAllAnswersChecked) || isDisabled,
            "_no-checked": statusAnswerBlank == "AwaitingInspection" || (statusAnswerBlank == "AutoChecked" && !isAllAnswersChecked),
            _success: (statusAnswerBlank == "Checked" || (statusAnswerBlank == "AutoChecked" && isAllAnswersChecked)) && scores >= 51,
            _failed: (statusAnswerBlank == "Checked" || (statusAnswerBlank == "AutoChecked" && isAllAnswersChecked)) && scores < 51
        })}>
            {
                <Link to='/teacher/answers-check' state={{ ...dataForState, statusAnswerBlank, defaultScore, scores, isAllAnswersChecked }} className='score-circle__scores'>
                    {
                        ["AutoChecked", "Checked"].includes(statusAnswerBlank)
                            ? scores
                            : defaultScore
                    }
                </Link>
            }
        </div>
    )
}

export default ScoreCircle