import { useEffect, useState } from 'react'
import { Link, useLocation, useParams } from 'react-router-dom'
import { useFetching } from '../hooks/useFetching'
import AnswerBlankService from '../api/AnswerBlankService'
import Loader from '../components/ui/Loader/Loader'
import { getTotalScoreByAnswers } from '../utils/blank'
import ScoreCircle from '../components/ui/ScoreCircle/ScoreCircle'
import StudentAnswers from '../components/StudentAnswers'

const TestingStudentAnswers = () => {
  const { id: answerBlankId } = useParams()

  const location = useLocation()
  const { course, group, deptName, disciplineName } = location.state


  const [totalScore, setTotalScore] = useState(0)

  const [answers, setAnswers] = useState(null)
  const [getCheckedAnswerBlankById, isAnswerBlankLoading] = useFetching(async (answerBlankId) => {
    const response = await AnswerBlankService.getCheckedAnswerBlanksByIdForStudent(answerBlankId)
    if (response.status == 200) {
      setAnswers(response.data.answers)
      setTotalScore(getTotalScoreByAnswers(response.data.answers))
    }
  })

  useEffect(() => {
    getCheckedAnswerBlankById(answerBlankId)
  }, [])

  return (
    <section className='answers-check'>
      <div className="answers-check__container container container--smaller">
        {
          isAnswerBlankLoading
            ? <Loader text="  " />
            :
            <>
              <div className='answer-check__wrapper'>
                <Link className='btn btn_secondary' to={-1}></Link>
                <ScoreCircle
                  isDisabled={true}
                  isAllAnswersChecked={true}
                  scores={totalScore}
                  statusAnswerBlank={"Checked"}
                />
              </div>
              <div className="answers-check__body">
                <h1 className='answers-check__title title'>{disciplineName}</h1>
                <div className="answers-check__data data">
                  <span className='data__stage'>{`${course}  ${group} `}</span>
                  <span className='data__department'>{deptName}</span>
                </div>
               <StudentAnswers answers={answers} />
              </div>
            </>
        }
      </div>
    </section>
  )
}

export default TestingStudentAnswers