import { useState, useEffect, useContext } from 'react'
import Input from '../../components/ui/Input/Input'
import UserService from '../../api/UserService'
import { useFetching } from '../../hooks/useFetching'
import { Link, useLocation, useNavigate } from 'react-router-dom'
import Select from '../../components/ui/Select/Select'
import Button from '../../components/ui/Button/Button'
import { AppContext } from "../../context"
import TestingService from '../../api/TestingService'
import Loader from '../../components/ui/Loader/Loader'
import QuestionBlockForTestingService from '../../api/QuestionBlockForTestingService'

const UpdateTestingSecondStep = () => {
    const { showToast, employeeId } = useContext(AppContext)

    const redirect = useNavigate()
    
    const location = useLocation()
    const { testing, questions: updateQuestions, questionTypes } = location.state

    const [inspectorId, setInspectorId] = useState(testing.testingInspectorId)
    const [employees, setInspectors] = useState([])
    const [getInspectors, isInspectorsLoading] = useFetching(async () => {
        const response = await UserService.getInspectors()
        if (response.status == 200) {
            const dataArr = []
            response.data.forEach(dataItem => {
                dataArr.push({
                    value: dataItem.id,
                    label: dataItem.login
                })
            })

            setInspectors(dataArr)
        }
    })

    const [questionBlocksForTesting, setQuestionBlocksForTestiing] = useState([])
    const [getQuestionBlockForTestingByTestingId, isQuestionBlockLoading] = useFetching(async (testingId) => {
        const response = await QuestionBlockForTestingService.getQuestionBlockForTestingByTestingId(testingId)
        if (response.status == 200) {
            setQuestionBlocksForTestiing(response.data)
        }
    })


    const [updateTesting, isUpdateLoading] = useFetching(async (updateTestingData) => {
        const response = await TestingService.updateTesting(updateTestingData)
        if (response.status == 200) {
            showToast("success", "  !")
            redirect(`/uko/${employeeId}`)
        }
    })

    const getQuestionTypeIdByName = (questionTypeName) => {
        const qType = questionTypes.find(x => x.questionTypeName == questionTypeName)
        return qType?.id
    }

    useEffect(() => {
        getInspectors()
        getQuestionBlockForTestingByTestingId(updateQuestions[0].testingId)
    }, [])

    const isValidateFields = () => {
        let isValid = true
        let errorMessage = ""

        const inspectorSelect = document.querySelector(".update-testing__select")
        if (!inspectorId) {
            isValid = false;
            errorMessage = ",  ,  !"
            inspectorSelect.classList.add("_error")

            return { isValid, errorMessage }
        } else {
            inspectorSelect.classList.remove("_error")
        }

        const questionCountFields = document.querySelectorAll('input[name^="question-"]')
        for (let field of questionCountFields) {
            if (!field.value) {
                isValid = false;
                errorMessage = "   !"
                field.classList.add("_error")
                return { isValid, errorMessage }
            } else {
                field.classList.remove("_error")
            }
        }

        const blockScoreFields = document.querySelectorAll('input[name^="block-"]')
        let totalBlockScores = 0
        for (let field of blockScoreFields) {
            if (!field.value) {
                isValid = false;
                errorMessage = "   !"
                field.classList.add("_error")
                return { isValid, errorMessage }
            } else {
                field.classList.remove("_error")
            }

            totalBlockScores += parseInt(field.value)
            if (totalBlockScores > 100) {
                isValid = false;
                errorMessage = " -        100 !"
                return { isValid, errorMessage }
            }
        }

        return { isValid, errorMessage }
    }

    const handleCreateTesting = () => {
        const { isValid, errorMessage } = isValidateFields()
        if (isValid) {
            const updateQuestionBlockForTestings = [
                {
                    id: questionBlocksForTesting[0].id,
                    testingId: questionBlocksForTesting[0].testingId,
                    questionBlock: 1,
                    updateQuestionTypeForTestings: [
                        {
                            id: questionBlocksForTesting[0].questionTypeForTestings[0].id,
                            questionTypeId: getQuestionTypeIdByName("2"),
                            questionBlockForTestingId: questionBlocksForTesting[0].questionTypeForTestings[0].questionBlockForTestingId,
                            questionCount: parseInt(document.querySelector('input[name="question-two"]').value)
                        },
                        {
                            id: questionBlocksForTesting[0].questionTypeForTestings[1].id,
                            questionTypeId: getQuestionTypeIdByName("3"),
                            questionBlockForTestingId: questionBlocksForTesting[0].questionTypeForTestings[1].questionBlockForTestingId,
                            questionCount: parseInt(document.querySelector('input[name="question-three"]').value)
                        },
                        {
                            id: questionBlocksForTesting[0].questionTypeForTestings[2].id,
                            questionTypeId: getQuestionTypeIdByName("4"),
                            questionBlockForTestingId: questionBlocksForTesting[0].questionTypeForTestings[2].questionBlockForTestingId,
                            questionCount: parseInt(document.querySelector('input[name="question-four"]').value)
                        }
                    ],
                    scores: parseInt(document.querySelector('input[name="block-one-scores"]').value)
                },
                {
                    id: questionBlocksForTesting[1].id,
                    testingId: questionBlocksForTesting[1].testingId,
                    questionBlock: 2,
                    updateQuestionTypeForTestings: [
                        {
                            id: questionBlocksForTesting[1].questionTypeForTestings[0].id,
                            questionTypeId: getQuestionTypeIdByName("1"),
                            questionBlockForTestingId: questionBlocksForTesting[1].questionTypeForTestings[0].questionBlockForTestingId,
                            questionCount: parseInt(document.querySelector('input[name="question-one"]').value)
                        },
                        {
                            id: questionBlocksForTesting[1].questionTypeForTestings[1].id,
                            questionTypeId: getQuestionTypeIdByName("5"),
                            questionBlockForTestingId: questionBlocksForTesting[1].questionTypeForTestings[1].questionBlockForTestingId,
                            questionCount: parseInt(document.querySelector('input[name="question-five"]').value)
                        }
                    ],
                    scores: parseInt(document.querySelector('input[name="block-two-scores"]').value)
                },
                {
                    id: questionBlocksForTesting[2].id,
                    testingId: questionBlocksForTesting[2].testingId,
                    questionBlock: 3,
                    updateQuestionTypeForTestings: [
                        {
                            id: questionBlocksForTesting[2].questionTypeForTestings[0].id,
                            questionTypeId: getQuestionTypeIdByName(""),
                            questionBlockForTestingId: questionBlocksForTesting[2].questionTypeForTestings[0].questionBlockForTestingId,
                            questionCount: parseInt(document.querySelector('input[name="question-case"]').value)
                        }
                    ],
                    scores: parseInt(document.querySelector('input[name="block-three-scores"]').value)
                }
            ]

            const RETAKES_NUMBER = {
                "FirstRetake": 1,
                "SecondRetake": 2,
                "ThirdRetake": 3
            }

            for (let question of updateQuestions) {
                question["updateAnswerVariants"] = question["answerVariants"];
                delete question["answerVariants"];
            }

            const updateTestingData = {
                id: testing.testingId,
                timetableSessionId: testing.timeTableSessionId,
                testDurationOnMinutes: testing.testDurationOnMinutes,
                testingDate: testing.testingDate,
                retake: RETAKES_NUMBER[testing.retake],
                disciplineId: testing.disciplineId,
                disciplineName: testing.disciplineName,
                testingCreatorId: testing.testingCreatorId,
                testingInspectorId: inspectorId,
                updateQuestionBlockForTestings,
                updateQuestions
            }

            updateTesting(updateTestingData)
        } else {
            showToast("error", errorMessage)
        }
    }

    return (
        <div className='update-testing'>
            <div className="update-testing__container container">
                <h1 className='update-testing__title title'> 2.    </h1>
                <div className='update-testing__teachers'>
                    <span className='update-testing__teachers-label'>    </span>
                    <div className='update-testing__select'>
                        {
                            isInspectorsLoading
                                ? <Loader text="   " />
                                :
                                <Select
                                    value={employees?.find(x => x.value == inspectorId)}
                                    onChange={(newValue) => { setInspectorId(newValue.value) }}
                                    placeholder=' '
                                    options={employees}
                                    isLoading={isInspectorsLoading}
                                    isDisabled={isInspectorsLoading}
                                />
                        }
                    </div>
                </div>
                {
                    isQuestionBlockLoading
                        ? <Loader text="" />
                        :
                        <div className='update-testing__table'>
                            <table>
                                <thead>
                                    <tr>
                                        <th> </th>
                                        <th>   </th>
                                        <th>   </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td colSpan={3}> 1</td>
                                    </tr>
                                    <tr>
                                        <td>2</td>
                                        <td>
                                            <Input value={questionBlocksForTesting[0]?.questionTypeForTestings[0]?.questionCount} min={1} onWheel={() => document.activeElement.blur()} isSmall={true} className="update-testing__input" onInput={e => {
                                                if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                            }} name="question-two" type="number" />
                                        </td>
                                        <td rowSpan={3}>
                                            <Input value={questionBlocksForTesting[0]?.scores} min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                                if (e.target.value > 100) { e.target.value = 100 }
                                            }} isSmall={true} className="update-testing__input" name="block-one-scores" type="number" />
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>3</td>
                                        <td>
                                            <Input value={questionBlocksForTesting[0]?.questionTypeForTestings[1]?.questionCount} min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                                if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                            }} isSmall={true} className="update-testing__input" name="question-three" type="number" />
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>4</td>
                                        <td>
                                            <Input value={questionBlocksForTesting[0]?.questionTypeForTestings[2]?.questionCount} min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                                if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                            }} isSmall={true} className="update-testing__input" name="question-four" type="number" />
                                        </td>
                                    </tr>
                                    <tr>
                                        <td colSpan={3}> 2</td>
                                    </tr>
                                    <tr>
                                        <td>1</td>
                                        <td>
                                            <Input value={questionBlocksForTesting[1]?.questionTypeForTestings[0]?.questionCount} min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                                if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                            }} isSmall={true} className="update-testing__input" name="question-one" type="number" />
                                        </td>
                                        <td rowSpan={2}>
                                            <Input value={questionBlocksForTesting[1]?.scores} min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                                if (e.target.value > 100) { e.target.value = 100 }
                                            }} isSmall={true} className="update-testing__input" name="block-two-scores" type="number" />
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>5</td>
                                        <td>
                                            <Input value={questionBlocksForTesting[1]?.questionTypeForTestings[1]?.questionCount} min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                                if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                            }} isSmall={true} className="update-testing__input" name="question-five" type="number" />
                                        </td>
                                    </tr>
                                    <tr>
                                        <td colSpan={3}> 3</td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>
                                            <Input value={questionBlocksForTesting[2]?.questionTypeForTestings[0]?.questionCount} min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                                if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                            }} isSmall={true} className="update-testing__input" name="question-case" type="number" />
                                        </td>
                                        <td>
                                            <Input value={questionBlocksForTesting[2]?.scores} min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                                if (e.target.value > 100) { e.target.value = 100 }
                                            }} isSmall={true} className="update-testing__input" name="block-three-scores" type="number" />
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                }
                <div className='update-testing__btns'>
                    <Button onClick={handleCreateTesting} className="update-testing__save" isDisabled={isUpdateLoading}>
                        {isUpdateLoading ? <Loader text=" " /> : " "}
                    </Button>
                    <Link className='update-testing__back btn btn_secondary' to={-1}></Link>
                </div>
            </div>
        </div >
    )
}

export default UpdateTestingSecondStep