import {useContext} from 'react';
import {AppContext} from "../context";
import Input from '../components/ui/Input/Input';
import Button from '../components/ui/Button/Button';
import { useNavigate } from 'react-router-dom';
import { useFetching } from '../hooks/useFetching';
import { Controller, useForm } from 'react-hook-form';
import UserService from '../api/UserService';
import Loader from '../components/ui/Loader/Loader';


const LoginAdmin = () => {

    const {setIsAuthAdmin, setUserName, setAccessToken, setRoleName, showToast, setEmployeeId, setUserFilialId} = useContext(AppContext);


    const redirect = useNavigate()

    const [authUser, isLoginLoading, loginError] = useFetching(async (loginUser, passwordUser) => {
        const response = await UserService.signIn(loginUser, passwordUser)

        if (response.status == 200) {
            let userData = response.data
            setIsAuthAdmin(true)
            localStorage.setItem("isAuthAdmin", "true")

            localStorage.setItem("employeeId", userData.userId)
            setEmployeeId(userData.userId)
            localStorage.setItem("userName", userData.userName)
            setUserName(userData.userName)

            if (userData.filialId) {
                localStorage.setItem("filialId", userData.filialId)
                setUserFilialId(userData.filialId)
            }

            localStorage.setItem("access_token", userData.jwtToken)
            setAccessToken(userData.jwtToken)

            localStorage.setItem("roleName", userData.role)
            setRoleName(userData.role)
            redirect(`/${userData.role}/${userData.role != "admin" && userData.userId}`) 
        } else {
            showToast("error", response.data)
        }
    })

    const { control, handleSubmit } = useForm({
        mode: "onSubmit"
    })

    const login = (data) => {
        authUser(data.loginUser, data.passwordUser)
    }

    return (
        <section className='login'>
            <div className='container'>
                <div className='login__inner'>
                    <h1 className='login__title title'>  </h1>
                    <form onSubmit={handleSubmit(login)} className='form'>
                        <label className='form__label'>
                            <span className='form__text'></span>
                            <Controller
                                control={control}
                                name='loginUser'
                                rules={{
                                    required: true
                                }}
                                render={({field: {onChange}, fieldState: { error }}) => (
                                    <Input 
                                        className="form__input"
                                        onChange={(newValue) => {onChange(newValue)}}
                                        isErrorStatus={!!error}
                                    />
                                )}
                            />    
                        </label>
                        <label className='form__label'>
                            <span className='form__text'></span>
                            <Controller
                                control={control}
                                name='passwordUser'
                                rules={{
                                    required: true
                                }}
                                render={({field: {onChange}, fieldState: { error }}) => (
                                    <Input
                                        type="password" 
                                        className="form__input"
                                        onChange={(newValue) => {onChange(newValue)}}
                                        isErrorStatus={!!error}
                                    />
                                )}
                            />    
                        </label>
                        <Button className="form__btn" isDisabled={isLoginLoading}>
                            {isLoginLoading ? <Loader text="" /> : ""}
                        </Button>
                    </form>
                </div>
            </div>
        </section>
    );
};

export default LoginAdmin;
