import { useParams } from 'react-router-dom'
import { useFetching } from '../../hooks/useFetching'
import TestingService from '../../api/TestingService'
import { useEffect, useState } from 'react'
import TestingListForTeacher from '../../components/teacher/TestingListForTeacher'
import Loader from '../../components/ui/Loader/Loader'

const TestingsForTeacher = () => {
  const { id: employeeId } = useParams()

  const [testings, setTestings] = useState([])
  const [getTestingsByEmployeeId, isTestingsLoading] = useFetching(async (employeeId) => {
    const response = await TestingService.getTestingsByEmployeeId(employeeId)

    if (response.status == 200) {
      setTestings(response.data)
    }
  })

  useEffect(() => {
    getTestingsByEmployeeId(employeeId)
  }, [])

  return (
    <section className='testings'>
      <div className='container'>
        <h1 className='testings__title title'></h1>
          {
            isTestingsLoading
              ? <Loader text="  " /> 
              : <TestingListForTeacher testings={testings} />
          } 
      </div>
    </section>
  )
}

export default TestingsForTeacher