import axios from "axios";
import { API_URL, getToken } from "./config";

export default class QuestionService {
    static async getQuestionsByTestingId(id) {
        const response = await axios.get(`${API_URL}/Question/GetQuestionsByTestingId?testingId=${id}`, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }
}