import { useState, useEffect, useContext } from 'react'
import Input from '../../components/ui/Input/Input'
import UserService from '../../api/UserService'
import { useFetching } from '../../hooks/useFetching'
import { Link, useLocation, useNavigate } from 'react-router-dom'
import Select from '../../components/ui/Select/Select'
import Button from '../../components/ui/Button/Button'
import {AppContext} from "../../context"
import TestingService from '../../api/TestingService'
import Loader from '../../components/ui/Loader/Loader'

const CreateTestingSecondStep = () => {
    const {showToast, employeeId} = useContext(AppContext)
    
    const redirect = useNavigate()
    
    const location = useLocation()
    const {ttSessions, questions: createQuestions, questionTypes} = location.state

    const [inspectorId, setInspectorId] = useState(null)
    const [inspectors, setInspectors] = useState([])
    const [getInspectors, isInspectorsLoading] = useFetching(async () => {
        const response = await UserService.getInspectors()
        if (response.status == 200) {
            const dataArr = []
            response.data.forEach(dataItem => {
                dataArr.push({
                    value: dataItem.id,
                    label: dataItem.login
                })
            })

            setInspectors(dataArr)
        }
    })

    const [createTesting, isCreateLoading] = useFetching(async (createTestingData) => {
        const response = await TestingService.createTesting(createTestingData)
        if (response.status == 200) {
            showToast("success", "  !")
            redirect(`/uko/${employeeId}`)
        }
    })

    const getQuestionTypeIdByName = (questionTypeName) => {
        const qType = questionTypes.find(x => x.questionTypeName == questionTypeName)
        return qType?.id
    }

    useEffect(() => {
        getInspectors()
    }, [])

    const isValidateFields = () => {
        let isValid = true
        let errorMessage = ""

        const inspectorSelect = document.querySelector(".create-testing__select")
        if (!inspectorId) {
            isValid = false;
            errorMessage = ",  ,  !" 
            inspectorSelect.classList.add("_error")

            return {isValid, errorMessage} 
        } else {
            inspectorSelect.classList.remove("_error")
        }

        const questionCountFields = document.querySelectorAll('input[name^="question-"]')
        for (let field of questionCountFields) {
            if (!field.value) {
                isValid = false;
                errorMessage = "   !"
                field.classList.add("_error")
                return {isValid, errorMessage}
            } else {
                field.classList.remove("_error")
            }
        }

        const blockScoreFields = document.querySelectorAll('input[name^="block-"]')
        let totalBlockScores = 0
        for (let field of blockScoreFields) {
            if (!field.value) {
                isValid = false;
                errorMessage = "   !"
                field.classList.add("_error")
                return {isValid, errorMessage}
            } else {
                field.classList.remove("_error")
            }

            totalBlockScores += parseInt(field.value)
            if (totalBlockScores > 100) {
                isValid = false;
                errorMessage = " -        100 !"
                return {isValid, errorMessage}
            }
        }

        return {isValid, errorMessage}
    }

    const handleCreateTesting = () => {
        const {isValid, errorMessage} = isValidateFields()
        if (isValid) {
            const createQuestionBlockForTesting = [
                {
                    questionBlock: 1,
                    questionTypeForTestings: [
                        {
                            questionTypeId: getQuestionTypeIdByName("2"),
                            questionCount: parseInt(document.querySelector('input[name="question-two"]').value)
                        },
                        {
                            questionTypeId: getQuestionTypeIdByName("3"),
                            questionCount: parseInt(document.querySelector('input[name="question-three"]').value)
                        },
                        {
                            questionTypeId: getQuestionTypeIdByName("4"),
                            questionCount: parseInt(document.querySelector('input[name="question-four"]').value)
                        }
                    ],
                    scores: parseInt(document.querySelector('input[name="block-one-scores"]').value)
                },
                {
                    questionBlock: 2,
                    questionTypeForTestings: [
                        {
                            questionTypeId: getQuestionTypeIdByName("1"),
                            questionCount: parseInt(document.querySelector('input[name="question-one"]').value)
                        },
                        {
                            questionTypeId: getQuestionTypeIdByName("5"),
                            questionCount: parseInt(document.querySelector('input[name="question-five"]').value)
                        }
                    ],
                    scores: parseInt(document.querySelector('input[name="block-two-scores"]').value)
                },
                {
                    questionBlock: 3,
                    questionTypeForTestings: [
                        {
                            questionTypeId: getQuestionTypeIdByName(""),
                            questionCount: parseInt(document.querySelector('input[name="question-case"]').value)
                        }
                    ],
                    scores: parseInt(document.querySelector('input[name="block-three-scores"]').value)
                }
            ]
            
            const ttSessionsIterable = Array.isArray(ttSessions) ? ttSessions : [ttSessions]
            const createTestingDataArr = []
            const RETAKES_NUMBER = {
                "First": 1,
                "Second": 2,
                "Comission": 3
            }

            for (let session of ttSessionsIterable) {
                const createTestingData = {
                    timetableSessionId: session.id,
                    testDurationOnMinutes: session.duration,
                    testingDate: session.startEvent,
                    retake: RETAKES_NUMBER[session.retake],
                    disciplineId: session.subjectId,
                    disciplineName: session.subjectName,
                    testingCreatorId: employeeId,
                    testingInspectorId: inspectorId,
                    createQuestionBlockForTesting,
                    createQuestions
                }
                createTestingDataArr.push(createTestingData)
            }
            
            createTesting(createTestingDataArr)
        } else {
            showToast("error", errorMessage)
        }
    }

    return (
        <div className='create-testing'>
            <div className="create-testing__container container">
                <h1 className='create-testing__title title'> 2.    </h1>
                <div className='create-testing__teachers'>
                    <span className='create-testing__teachers-label'>    </span>
                    <div className='create-testing__select'>
                        <Select
                            onChange={(newValue) => { setInspectorId(newValue.value) }}
                            placeholder=' '
                            options={inspectors}
                            isLoading={isInspectorsLoading}
                            isDisabled={isInspectorsLoading}
                        />
                    </div>
                </div>
                <div className='create-testing__table'>            
                    <table>
                        <thead>
                            <tr>
                                <th> </th>
                                <th>   </th>
                                <th>   </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td colSpan={3}> 1</td>
                            </tr>
                            <tr>
                                <td>2</td>
                                <td>
                                    <Input min={1} onWheel={() => document.activeElement.blur()} isSmall={true} className="create-testing__input" onInput={e => {
                                        if (e.target.value.length > 4) {e.target.value = e.target.value.slice(0, 4)}
                                    }} name="question-two" type="number" />
                                </td>
                                <td rowSpan={3}>
                                    <Input min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                        if (e.target.value > 100) {e.target.value = 100}
                                    }} isSmall={true} className="create-testing__input" name="block-one-scores" type="number" />
                                </td>
                            </tr>
                            <tr>
                                <td>3</td>
                                <td>
                                    <Input min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                        if (e.target.value.length > 4) {e.target.value = e.target.value.slice(0, 4)}
                                    }} isSmall={true} className="create-testing__input" name="question-three" type="number" />
                                </td>
                            </tr>
                            <tr>
                                <td>4</td>
                                <td>
                                    <Input min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                        if (e.target.value.length > 4) {e.target.value = e.target.value.slice(0, 4)}
                                    }} isSmall={true} className="create-testing__input" name="question-four" type="number" />
                                </td>
                            </tr>
                            <tr>
                                <td colSpan={3}> 2</td>
                            </tr>
                            <tr>
                                <td>1</td>
                                <td>
                                    <Input min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                        if (e.target.value.length > 4) {e.target.value = e.target.value.slice(0, 4)}
                                    }} isSmall={true} className="create-testing__input" name="question-one" type="number" />
                                </td>
                                <td rowSpan={2}>
                                    <Input min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                        if (e.target.value > 100) {e.target.value = 100}
                                    }} isSmall={true} className="create-testing__input" name="block-two-scores" type="number" />
                                </td>
                            </tr>
                            <tr>
                                <td>5</td>
                                <td>
                                    <Input min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                        if (e.target.value.length > 4) {e.target.value = e.target.value.slice(0, 4)}
                                    }} isSmall={true} className="create-testing__input" name="question-five" type="number" />
                                </td>
                            </tr>
                            <tr>
                                <td colSpan={3}> 3</td>
                            </tr>
                            <tr>
                                <td></td>
                                <td>
                                    <Input min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                        if (e.target.value.length > 4) {e.target.value = e.target.value.slice(0, 4)}
                                    }} isSmall={true} className="create-testing__input" name="question-case" type="number" />
                                </td>
                                <td>
                                    <Input min={1} onWheel={() => document.activeElement.blur()} onInput={e => {
                                        if (e.target.value > 100) {e.target.value = 100}
                                    }} isSmall={true} className="create-testing__input" name="block-three-scores" type="number" />
                                </td>
                            </tr>    
                        </tbody>
                    </table>
                </div>
                <div className='create-testing__btns'>
                    <Button onClick={handleCreateTesting} className="create-testing__save" isDisabled={isCreateLoading}>
                        {isCreateLoading ? <Loader text=" " /> : " "}
                    </Button>
                    <Link className='create-testing__back btn btn_secondary' to={-1}></Link>
                </div>
            </div>
        </div >
    )
}

export default CreateTestingSecondStep