import "./SingleCheckBox.css"

const SingleCheckBox = ({name, numberKey, onClick}) => {
  return (
    <div className="checkbox-item" key={`${name}-${numberKey}`}>
        <input type="checkbox" name={name} id={`${name}-${numberKey}`} onClick={(e) => {
            e.target.closest(".checkbox-item").classList.toggle("_checked"); 
            onClick(e.target.checked)
        }} />
        <label htmlFor={`${name}-${numberKey}`}></label>
      </div>
  )
}

export default SingleCheckBox