import { useContext, useEffect, useState } from 'react'
import Button from '../../components/ui/Button/Button'
import AnswerList from '../../components/teacher/AnswerList'
import { Link, useNavigate, useLocation } from 'react-router-dom'
import { useFetching } from '../../hooks/useFetching'
import AnswerBlankService from '../../api/AnswerBlankService'
import QuestionTypeService from '../../api/QuestionTypeService'
import Loader from '../../components/ui/Loader/Loader'
import { getTotalScoreByAnswers } from '../../utils/blank'
import { getQuestionTypeNameById } from '../../utils/questionType'
import ScoreCircle from '../../components/ui/ScoreCircle/ScoreCircle'
import { AppContext } from '../../context'
import Popup from '../../components/ui/Popup/Popup'

const CheckingAnswers = () => {
  const location = useLocation()
  const { course, group, deptName, disciplineName, defaultScore, scores, statusAnswerBlank, isAllAnswersChecked, answers } = location.state

  const {showToast} = useContext(AppContext)

  const [totalScore, setTotalScore] = useState(scores || 0)
  const [modalActive, setModalActive] = useState(false)
  const redirect = useNavigate()

  const [questionTypes, setQuestionTypes] = useState([])
  const [getQuestionTypes, isTypesLoading] = useFetching(async () => {
    const response = await QuestionTypeService.getQuestionTypes()
    if (response.status == 200) {
      setQuestionTypes(response.data)
      
      const answerForChecking = answers.filter(x => ["1", "5", ""].includes(getQuestionTypeNameById(response.data, x.question.questionTypeId)))
      const res = []

      for (let answer of answerForChecking) {
        if (answer.numberOfPoints) {
          res.push({
            answerId: answer.id,
            numberOfPoints: answer.numberOfPoints
          })
        }
      }

      setListEvaluation(res)
    }
  })


  const [listEvaluation, setListEvaluation] = useState([])

  const [saveAnswers, isSaveLoading] = useFetching(async (answerBlankId, listEvaluations, isEndChecking) => {
    const response = await AnswerBlankService.evaluationOfAnswers(answerBlankId, listEvaluations, isEndChecking)
    if (response.status == 200) {
      showToast("success", "  !")
      redirect(-1)
    }
  })

  useEffect(() => {
    getQuestionTypes()
  }, [])

  const handleUpdateListEvalutaion = (data) => {
    const res = [...listEvaluation]

    const ind = res.findIndex(x => x.answerId == data.answerId)
    if (ind != -1) {
      res[ind].numberOfPoints = isNaN(data.numberOfPoints) ? null : data.numberOfPoints
    } else {
      res.push(data)
    }

    setListEvaluation(res)
    setTotalScore(getTotalScoreByAnswers(res))
  }

  const handleEndChecking = () => {
    if (totalScore > 100) {
      showToast("error", " -       100 !")
    } else {
      setModalActive(true)
    }
  }

  const onEndChecking = () => {
    const answerForChecking = answers.filter(x => ["1", "5", ""].includes(getQuestionTypeNameById(questionTypes, x.question.questionTypeId)))
    const isEndCheking = answerForChecking.length == listEvaluation.length
    saveAnswers(answers[0].answerBlankId, listEvaluation, isEndCheking)
  }

  return (
    <section className='answers-check'>
      <div className="answers-check__container container container--smaller">
        <div className='answer-check__wrapper'>
          <Link className='btn btn_secondary' to={-1}></Link>
          <ScoreCircle
            isDisabled={true}
            defaultScore={defaultScore}
            isAllAnswersChecked={isAllAnswersChecked}
            scores={getTotalScoreByAnswers(answers)}
            statusAnswerBlank={statusAnswerBlank}
          />
        </div>
        <div className="answers-check__body">
          <h1 className='answers-check__title title'>{disciplineName}</h1>
          <div className="answers-check__data data">
            <span className='data__stage'>{`${course}  ${group} `}</span>
            <span className='data__department'>{deptName}</span>
          </div>
          {
            isTypesLoading
              ? <Loader text="" />
              : <AnswerList answers={answers} questionTypes={questionTypes} onChange={(data) => handleUpdateListEvalutaion(data)} />
          }
          {
            answers.filter(x => ["1", "5", ""].includes(getQuestionTypeNameById(questionTypes, x.question.questionTypeId))).length > 0 
            &&
              <div className='answers-check__bottom'>
                <Button className='answers-check__btn' onClick={handleEndChecking}> </Button>
              </div>
          }
        </div>
      </div>
      <Popup active={modalActive} setActive={setModalActive}>
        <h2 className="popup__title title">    ?</h2>
        <div className="confirm-buttons">
          <Button isDisabled={isSaveLoading} onClick={onEndChecking}>
            {isSaveLoading ? <Loader /> : ""}
          </Button>
          <Button onClick={() => setModalActive(false)}></Button>
        </div>
      </Popup>
    </section>
  )
}

export default CheckingAnswers