import { Switch, Case } from "../SwitchCase"
import RadioGroup from "../ui/RadioGroup/RadioGroup"
import Input from "../ui/Input/Input"
import TextArea from "../ui/TextArea/TextArea"
import { getQuestionTypeNameById } from "../../utils/questionType"

const AnswerList = ({ answers, questionTypes, onChange }) => {
  const getDefaultVariants = (qVariants, sVariants) => {
    const res = []

    if (sVariants) {
      for (let i = 0; i < sVariants.length; i++) {
        let qVar = { ...qVariants.find(x => x.id == sVariants[i].answerVariantId) }
        qVar.numberAnswerVariant = sVariants[i].numberAnswerVariant ? sVariants[i].numberAnswerVariant : 0
        res.push(qVar)
      }
    }

    return res
  }

  const handleSetScores = (scores, answerId) => {  
    onChange && onChange({
      answerId,
      numberOfPoints: scores
    })
  }

  return (
    <ul className='answers-check__list'>
      {
        answers
          .filter(x => ["1", "5", ""].includes(getQuestionTypeNameById(questionTypes, x.question.questionTypeId)))
          .map((answerItem, index) => {
          return (
            <li key={answerItem.id} className='answers-check__item answers-item'>
              <div className="answers-item__wrapper">
                <span className='answers-item__number'> {index + 1}</span>
                <div className="answers-item__text" dangerouslySetInnerHTML={{ __html: answerItem.question.questionText.replace("align=left", "") }}></div>
              </div>
              <div className="answers-item__wrapper">
                <span className='answers-item__label'></span>
                <div className="answers-item__body">
                  <Switch param={getQuestionTypeNameById(questionTypes, answerItem.question.questionTypeId)}>
                    <Case value="1">
                      <RadioGroup showIsTrueAnswer={true} isClearable={false} value={getDefaultVariants(answerItem.question.answerVariants, answerItem.studentAnswerVariants)[0]} name={`question-${index + 1}`} variants={answerItem.question.answerVariants} />
                      <span className="answers-item__label"></span> 
                      <TextArea heightByContent={true} value={answerItem?.studentAnswerJustification} onPaste={(evt) => evt.preventDefault()} className='answer-variants__justification' />
                      <span className="answers-item__label"></span>  
                      <TextArea heightByContent={true} value={answerItem?.question?.justification} onPaste={(evt) => evt.preventDefault()} className='answer-variants__justification' />
                    </Case>
                    <Case value="5">
                      <Input value={answerItem?.text} onPaste={(evt) => evt.preventDefault()} />
                      <span className="answers-item__label"></span> 
                      <TextArea heightByContent={true} value={answerItem?.studentAnswerJustification} onPaste={(evt) => evt.preventDefault()} className='answer-variants__justification' />
                      <span className="answers-item__label"></span>  
                      <TextArea heightByContent={true} value={answerItem?.question?.justification} onPaste={(evt) => evt.preventDefault()} className='answer-variants__justification' />
                    </Case>
                    <Case value="">
                      <TextArea heightByContent={true} value={answerItem?.text} onPaste={(evt) => evt.preventDefault()} />
                      <span className="answers-item__label"></span>  
                      <TextArea heightByContent={true} value={answerItem?.question?.justification} onPaste={(evt) => evt.preventDefault()} className='answer-variants__justification' />
                    </Case>
                  </Switch>
                  <div className="answers-item__scores">
                    : <Input value={answerItem?.numberOfPoints} min={1} type="number" onWheel={() => document.activeElement.blur()} isSmall={true} onInput={e => {
                                        if (e.target.value.length > 5) {e.target.value = e.target.value.slice(0, 5)};
                                        handleSetScores(parseFloat(e.target.value.replace(",", ".")), answerItem.id)
                                    }} />
                  </div>
                </div>
              </div>
            </li>
          )
        })
      }
    </ul>

  )
}

export default AnswerList