import { Link, useNavigate } from 'react-router-dom'
import { Case, Switch } from '../SwitchCase'
import { getTestingFormatDate, getTestingStatusClassByStatus } from '../../utils/testing'
import clsx from 'clsx'
import Button from '../ui/Button/Button'
import { useContext } from 'react'
import { UkoContext } from '../../context'

const TestingsItemForUko = ({ testing }) => {
  const {setSelectedCurrentTestingId, setModalDeleteActive} = useContext(UkoContext)
  const redirect = useNavigate()

  return (
    <li className={clsx("testings__item", "testings-item", getTestingStatusClassByStatus(testing.testingStatus))}>
      <span className='testings-item__date'>{getTestingFormatDate(testing.testingDate)}</span>
      <div className='testings-item__data data'>
        <span className='data__stage'>{`${testing.course} ${testing.nGroup ? ` ${testing.nGroup} ` : ""}`}</span>
        <span className='data__department'>{testing.department}</span>
      </div>
      <div className="testings-item__btns">
        {
          <Switch param={testing.testingStatus}>
            <Case value="Created">
              <div className='discipline-btn'>{testing.disciplineName}</div>
              <Button onClick={() => redirect(`/uko/update/1`, { state: testing })} className='btn_edit'></Button>
              <Button onClick={() => redirect(`/uko/preview/`, { state: {testingId: testing.testingId, testingInspector: testing.testingInspector } })} className='btn_preview'></Button>
              <Button className="btn_remove" onClick={() => {
                setSelectedCurrentTestingId(testing.testingId);
                setModalDeleteActive(true)
              }} />
            </Case>
            <Case value={["Started", "Finished"]}>
              <div className='discipline-btn'>{testing.disciplineName}</div>
            </Case>
            <Case value="Checked">
              <Link to={`/testing-results/${testing.testingId}`} state={ { course: testing.course, group: testing.nGroup, deptName: testing.deptName, disciplineName: testing.disciplineName } } className='discipline-btn'>{testing.disciplineName}</Link>
            </Case>
          </Switch>
        }
      </div>
    </li>
  )
}

export default TestingsItemForUko